/* variaveis */

/* general */
body {
  background-color: #212121;
}
section, header {
  height: 100vh;
}

h2.center-align {
  margin: 0;
}

h2 {
  padding: 40px;
}

*, *:after, *:before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* nav */

nav ul a {
  color: black;
}
/* header */
header {
  position: relative;
  display: flex;
  align-items: center;
  background-image: url("../../src/images/background.jpg");
  background-color: black;
  filter: grayscale(80%);
  color: white;
  height: 80vh !important;
  min-height: 90vh !important;

  &::before {
    content: " ";
    position: absolute;
    width: 100vw;
    height: 100%;
    background-color: black;
    opacity: 0.8;
    z-index: -1;
  }
}

.header-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
}

#profilePicture {
  margin: 2em;
  height: 180px;
  width: 180px;
  border-radius: 100%;
}

header .description {
  text-align: center;
}


/* sobre */

section#about {
  .col {
    height: 100vh;
  }

  .icons {
    display: flex;
    flex-flow: column;
    font-size: 7em;
    align-items: center;
    justify-content: space-around;
  }
  
}

/*portfolio*/

#portfolio {
  height: 200vh;
  width: 100vw;

  .case1, .case2 {
    position: relative;
    display: flex;
    justify-content: center;
    margin-top: 25px;
    
    div {
      position: absolute;
      display: flex;
      justify-content: center;
      background-color: #eeeeee;
      width: 800px;
      height: 630px;
      top: 1em;
      right:265px;
    }

    a {
      z-index: 2;
    }
  }
}

/*footer*/
footer#contact .social-medias {
  text-align: center;
  font-size: 40px;
  
}

footer#contact .social-medias a {
  color: #212121;
}

 footer {
   margin-top: 25px;
   text-align: center;
   height: 20vh;
 }


